import React, {Fragment} from 'react';
import classNames from 'classnames';
import {format} from "date-fns";

export default function Streams(props) {
    const {streams, version, events} = props;
    streams.forEach(stream => {
        stream.events = events.filter(event => event.camera == stream.id);
        stream.eventCount = stream.events.reduce((sum, event) => sum + event.total, 0);
        stream.todayCount = stream.events.filter(event => event.date === format(new Date(), "yyyy-mm-dd"));
    })
    console.log(moment().format("yyyy-mm-dd"));
    let cameras = streams.map(stream =>
        <div id={'cam-' + stream.id} className={classNames("camera", stream.status)} key={stream.id}>
            <div className="header">
                <div className={classNames("events", {'has-events': !!stream.eventCount})}>
                    <div className="all">{stream.eventCount}</div>
                    <div className='today'>{stream.todayCount}</div>
                </div>
                <div className='last'/>
                <div className="name">{stream.name}</div>
            </div>
            <img src={stream.url} draggable="false"/>
        </div>
    );
    return (
        <Fragment>
            <div id="panel">{cameras}</div>
            <div id="version">{version}</div>
        </Fragment>
    );
}


